import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  // Redirect,
} from "react-router-dom";
import Home from "./containers/Home";
import Projects from './containers/Projects'

function App() {
  return (
    <Router>
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route exact path="/projects">
            <Projects />
          </Route>
          {/* <Redirect  /> */}
        </Switch>
    </Router>
  );
}

export default App;
